.time-rule[data-v-48151af3]{
    position: relative;
    width: 100%;
    margin: 0 auto;
    height: 50px;
    overflow: hidden;
    /* overflow: auto; */
    max-width: 1440px;
}
/* .time-rule::-webkits-scrollbar {display:none} */
.time-day[data-v-48151af3]{
    position: absolute;
    width: 1440px;
    background-color:#3c8dbc;
    height: 100%;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    cursor: pointer;
}
.time-text[data-v-48151af3]{
    float: left;
    width: 60px;
    border-top: 1px black solid;
    border-right: 1px black solid;
    text-align: center;
}
.time-minite[data-v-48151af3]{
    float: left;
    height: 8px;
    margin: 0px;
    width: 1px;
}
.time-cursor[data-v-48151af3]{
   position: absolute;
    left: 0;
    top: 0;
    height: 30px;
    width: 2px;
    background-color: red;
    text-align: center
}
.time-cursor-text[data-v-48151af3]{
    position: absolute;
    width: 60px;
    border: 1px solid red;
    height: 15px;
    padding: 0 5px;
    line-height: 15px;
    background-color: papayawhip;
    top:30px;
    left: -30px;
    cursor: move;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}
.green[data-v-48151af3]{
